import Vue from 'vue'
import Router from 'vue-router'
const  Fans = r => require.ensure([], ()=>r(require('@/components/fans/Index.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:'/',
      name:'Index',
      component:Fans
    }
  ]
})



